<template>
  <main>
    <div class="form-container container p-4">

      <!--Form starts-->
      <section class="step-header-container">
        <!--Form title-->
        <h5 class="step-title text-center font-weight-bold">
          Register your Product
        </h5>
        <!--Progress bar-->
        <div class="progress-container container py-4 px-3">
          <div class="progress m-0">
            <div class="determinate" :style="{ width: progress + '%' }"></div>
          </div>
        </div>
      </section>
      <!-- Step 1 -->
      <section id="step-1 pb-5 pt-5" v-if="step == 1">
        <div class="container pt-4">
          <h6>What do you want to post?</h6>
          <div class="row">
            <!--Category select-->
            <div class="col s12 pt-4">
              <label>Category</label>
              <select class="browser-default" v-model="formData.category">
                <option value="" disabled selected>Choose your option</option>
                <option
                  :value="category.value"
                  v-for="(category, index) in categories"
                  :key="index"
                  class="py-5"
                >
                  {{ category.name }}
                </option>
              </select>
            </div>
            <!--Title input-->
            <div class="col s12 mt-5 input-field">
              <input
                id="title"
                class="validate"
                type="text"
                data-length="10"
                v-model="formData.title"
                required
              />
              <label for="title">Title</label>
            </div>
          </div>
        </div>
      </section>
      <!-- Step 2 -->
      <section id="step-2 pb-5 pt-5" v-if="step == 2">
        <div class="container pt-4">
          <h6>Description</h6>
          <div class="row">
            <!--Price input-->
            <div class="col s12 m6 mt-4 input-field">
              <input
                id="price"
                class="validate"
                type="number"
                min="0"
                v-model="formData.price"
                required
              />
              <label for="price">Price</label>
            </div>
            <!--Description input-->
            <div class="col s12 input-field">
              <textarea
                id="description"
                class="materialize-textarea validate"
                v-model="formData.description"
                required
              ></textarea>
              <label for="description">Description</label>
            </div>
          </div>
        </div>
      </section>
      <!-- Step 3 -->
      <section id="step-3 pb-5 pt-5" v-if="step == 3">
        <div class="container pt-4">
          <h6>Description</h6>
          <div class="row">
            <!--Image input-->
            <div class="file-field input-field col s12">
              <div class="btn light-green">
                <span>
                  <i class="material-icons left">upload</i>
                  Upload
                </span>
                <input type="file" @change="filesHandler" />
              </div>
              <div class="file-path-wrapper">
                <input class="file-path validate" type="text" ref="imgName" />
              </div>
            </div>
          </div>
          <div class="row" v-if="images.length > 0">
            <ul class="collection">
              <li
                class="collection-item"
                v-for="(image, index) in images"
                :key="index"
              >
                <div>
                  {{ image.name }}
                  <a class="secondary-content" @click="deleteImage(index)">
                    <i class="material-icons red-text" style="cursor:pointer"
                      >delete</i
                    >
                  </a>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </section>

      <section class="options-section d-flex justify-content-around pt-5">
        <!--Previous step button-->
        <button
          class="btn waves-effect waves-light blue-grey-text blue-grey lighten-4 pr-4"
          @click.prevent="prevStep"
          :disabled="step == 1"
        >
          <i class="material-icons left mx-2">chevron_left</i>
          <span class="pr-2">back</span>
        </button>
        <!--Next step button-->
        <button
          class="btn waves-effect waves-light teal darken-1 pl-4"
          @click.prevent="nextStep"
          v-if="step < 3"
        >
          <span class="pl-2">next</span>
          <i class="material-icons right mx-2">chevron_right</i>
        </button>
        <!--Finish button-->
        <button
          @click.prevent="saveProduct"
          class="btn waves-effect waves-light teal darken-1 pl-4"
          v-if="step == 3"
          :disabled="isSaving"
        >
          <span class="pl-2">Finish</span>
          <i class="material-icons right mx-2">check</i>
        </button>
      </section>
    </div>
  </main>
<!-- Modal Structure -->
  <div id="modal1" class="modal">
    <div class="modal-content">
      <div class="progress">
        <div class="indeterminate"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations, mapState } from "vuex";
import router from "@/router";
export default {
  data() {
    return {
      step: 1,
      progress: (100 / 3) * 1,
      formData: {
      },
      categories: [
        {
          value: 1,
          name: "Art",
        },
        {
          value: 2,
          name: "Electronics",
        },
        {
          value: 3,
          name: "Vehicles",
        },
        {
          value: 3,
          name: "Sports",
        },
        {
          value: 4,
          name: "Real Estate",
        },
      ],
      images: [],
      currImage: null,
      isSaving: false,
      postLoader: null,
    };
  },
  computed: {
    ...mapGetters({
      imagesURLs: "imagesURLs",
    }),    
    ...mapState('user',['user']),
    setProgress() {
      this.progress = (100 / 3) * this.step;
    },
    urls() {
      return this.imagesURLs.length;
    },
  },
  methods: {
    ...mapMutations(["toggleLoader"]),
    ...mapActions(["uploadImage", "createPost"]),
    nextStep() {
      if (this.step < 3) {
        ++this.step;
        this.setProgress;
      }
    },
    prevStep() {
      if (this.step > 1) {
        --this.step;
        this.setProgress;
      }
    },
    filesHandler(e) {
      var files = e.target.files || e.dataTransfer.files;
      this.currImage = files[0];
      this.addImage();
    },
    addImage() {
      this.images.push(this.currImage);
      this.currImage = null;
      setTimeout(() => {
        this.$refs.imgName.value = "";
      }, 20);
    },
    deleteImage(index) {
      this.images.splice(index, 1);
    },
    saveProduct() {
      let data = this.formData;
      if (
        data.title != "" &&
        data.category != "" &&
        data.price > 0 &&
        data.description != "" &&
        this.images.length > 0
      ) {
        this.isSaving = true;
        this.postLoader.open()
        let urls = [];
        this.images.forEach((image) => {
          urls.push(this.uploadImage(image));
        });
      } else {
        M.toast({
          html:
            "<i class='material-icons left ml-3'>info_outline</i> Please fill out all the fields",
          classes: "red darken-2 pr-5",
        });
      }
    },
  },
  mounted() {
    var modal = document.querySelector(".modal");
    var modalInstance = M.Modal.init(modal, { dismissible: true });
    this.postLoader = M.Modal.getInstance(modal);
    this.toggleLoader();
    this.setProgress;
  },
  watch: {
    urls: function(value) {
      if (value === this.images.length) {
        this.formData.user = this.user
        this.createPost(this.formData);
      } else if (value === 0) {
        this.postLoader.close()
        router.push("/posts");
      }
    },
  },
};
</script>

<style scoped>
main {
  background: #f4f8f9 !important;
}
.form-container {
  min-height: 100vh;
  background: #fff;
}
.step-title {
  color: #263238 !important;
}
#modal1 {
  top: 50% !important;
  transform: scaleX(1) scaleY(1) translateY(-50%) !important;
}
</style>
